<template>
  <div class="article-edit-page">
    <!-- {{id}} -->
    <el-form
      :model="articleForm"
      :rules="rules"
      ref="articleForm"
      label-width="100px"
      class="demo-ruleForm"
      style="margin-top: 20px;"
    >
      <el-form-item label="" prop="title" required>
        <el-input v-model="articleForm.title"></el-input>
      </el-form-item>
      <el-form-item label="" prop="avatar">
        <el-select v-model="articleForm.avatar" placeholder="">
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="tags">
        <el-select v-model="articleForm.tags" multiple placeholder="">
          <el-option v-for="item in taglists" :key="item._id" :label="item.tag" :value="item._id"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="" prop="image" required>
        <el-input v-model="articleForm.image"></el-input>
        <el-upload
          class="avatar-uploader"
          :action="$http.defaults.baseURL + '/upload'"
          :show-file-list="false"
          :on-success="afterUpload"
        >
          <img v-if="articleForm.image" :src="articleForm.image" alt="" class="avatar" />
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item label="" prop="intro">
        <el-input type="textarea" v-model="articleForm.intro"></el-input>
      </el-form-item>

      <el-form-item label="" prop="resource">
        <el-select v-model="articleForm.resource" placeholder="">
          <el-option label="" value="0"></el-option>
          <el-option label="" value="1"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="body">
        <mavon-editor v-model="articleForm.body" ref="md" @imgAdd="imgAdd" />
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="updateArticle('articleForm')"></el-button>
        <el-button style="margin-left: 200px;" @click="resetForm('articleForm')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { mavonEditor } from "mavon-editor";
import "mavon-editor/dist/css/index.css";

export default {
  props: { id: {} },
  data() {
    return {
      taglists: [],
      value: [],
      // 
      articleForm: {
        title: "",
        avatar: "",
        tags: "",
        image: "",
        intro: "",
        resource: null, //  0  1 
        body: ""
      },
      rules: {
        title: [{ required: true, message: "", trigger: "blur" }],
        image: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          {
            pattern: /https?:\/{2}[^\s]*/,
            message: "httphttps"
          }
        ],
        resource: [
          { required: true, message: "", trigger: "change" }
        ],
        body: [{ required: true, message: "", trigger: "blur" }]
      }
    };
  },
  methods: {
    // markdowns
    async imgAdd(pos, file) {
      // ..
      const formdata = new FormData();
      formdata.append("file", file);
      const res = await this.$http.post("/upload", formdata);
      this.$refs.md.$img2Url(pos, res.data.filename);
    },

    // 
    afterUpload(res) {
      this.articleForm.image = res.filename;
    },

    // 
    updateArticle(ruleForm) {
      this.$refs[ruleForm].validate(valid => {
        if (valid) {
          this.$confirm(` "${this.articleForm.title}"`, "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "success"
          })
            .then(async () => {
              if (this.id) {
                this.articleForm = this.articleForm;
                await this.$http.put(`/article/${this.id}`, this.articleForm);
              } else {
                await this.$http.post(`/article`, this.articleForm);
              }
              this.$message({
                type: "success",
                message: "!"
              });
              this.$router.push("/article/list");
            })
            .catch(() => {
              this.$message({
                type: "info",
                message: ""
              });
            });
        } else {
          return false;
        }
      });
    },
    // 
    resetForm(articleForm) {
      this.$confirm(``, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$refs[articleForm].resetFields();
      });
    },
    // 
    async getTagList() {
      const res = await this.$http.get("/tag");
      this.taglists = res.data.data.tags_list;
    },

    // 
    async getArticleItem() {
      const res = await this.$http.get(`/article/${this.id}`);
      this.articleForm = res.data;
    }
  },
  created() {
    this.id && this.getArticleItem();
    this.getTagList();
  },
  components: {
    mavonEditor
  }
};
</script>

<style>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 356px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 356px;
  height: 178px;
  display: block;
}
</style>